import React from 'react';
import './App.css';
import {
    Panel,
    View,
    Root,
    PanelHeader,
    PanelHeaderBack, PanelHeaderContent, PanelHeaderContext
} from "@vkontakte/vkui";
import '@vkontakte/vkui/dist/vkui.css';
import fire from './functions/firebase';
import checkUserExists from './functions/checkUserExist';
import bridge from '@vkontakte/vk-bridge';
import getParams from './functions/getParams';
import {GoogleApiWrapper} from 'google-maps-react';
import $ from 'jquery';

import MapMain from "./components/MapMain";
import AreaGroupMain from "./components/areaData/groupMain";

import Icon16Dropdown from '@vkontakte/icons/dist/16/dropdown';

import mapStyle from "./const/mapStyle";
import polygons from "./const/polygon";
import AreaGroupPolitics from "./components/areaData/groupPolitics";
import AreaGroupActions from "./components/areaData/groupActions";
import UserHeaderContent from "./components/UserHeaderContent";
import DiversionPanel from "./components/DiversionPanel";
import AreaGroupHistory from "./components/areaData/groupHistrory";
import SpeechPanel from "./components/SpeechPanel";
import LawsPanel from "./components/LawsPanel";
import TradePanel from "./components/TradePanel";
import ProductionPanel from "./components/ProductionPanel";
import WarehousePanel from "./components/WarehousePanel";

class App extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            activeView: 'main',
            activePanel: 'main',
            VK_UID: 0,
            userData: {
                id: 0,
                warPoints: 0,
                points: []
            },
            popout: '',
            modal: '',
            snackbar: '',
            contextOpened: false,
            center: {
                lat: 54.779270,
                lng: 56.123964
            },
            zoom: 14,
            areas: [],
            users: [],
            activeArea: 0
        };
        this.openPanel = this.openPanel.bind(this);
        this.openCaptureModal = this.openCaptureModal.bind(this);
        this.polyClick = this.polyClick.bind(this);
        this.onDragged = this.onDragged.bind(this);
        this.toggleContext = this.toggleContext.bind(this);
    }
    componentDidMount() {
        this.setState({VK_UID: getParams('vk_user_id')});
        bridge.send("VKWebAppInit", {}).then(() => console.log('ВК ПОДКЛЮЧЕН')).catch(() => {window.location.reload()});
        checkUserExists(getParams('vk_user_id')).then(
            () => {
                let dataCol = fire.firestore().collection('data');
                // fire.firestore().collection('users').doc(getParams('vk_user_id').toString()).onSnapshot(listener => {
                //     this.setState({
                //         userData: listener.data()
                //     });
                // });
                dataCol.doc('areas').onSnapshot(listener => {
                    if(listener.data()){
                        this.setState({
                            areas: listener.data().main
                        });
                    }
                });
                dataCol.doc('users').onSnapshot(listener => {
                    if(listener.data()){
                        let d = listener.data().main;
                        let q = [];
                        // eslint-disable-next-line no-unused-vars
                        for (const [key, value] of Object.entries(d)) {
                            q.push(key.substr(1));
                        }
                        let w = q.join(',');
                        bridge.send("VKWebAppGetAuthToken", {"app_id": 7446907, "scope": ""}).then(
                            v => bridge.send("VKWebAppCallAPIMethod", {"method": "users.get", "request_id": "32test", "params": {"fields":"photo_200", "user_ids": w, "v":"5.110", "access_token":v.access_token}}).then(
                                v => {
                                    let re;
                                    for(let i = 0; i < v.response.length; i++){
                                        re = v.response[i];
                                        d['U'+re.id].photo_200 = re.photo_200;
                                        d['U'+re.id].first_name = re.first_name;
                                        d['U'+re.id].last_name = re.last_name;
                                        if(re.id === parseInt(getParams('vk_user_id'))){
                                           //...
                                        }
                                    }
                                    this.setState({
                                        users: d
                                    })
                                }
                            )
                        );
                    }
                });
            }
        );
    }
    componentWillUnmount() {

    }

    openPanel(panel){
        this.setState({
            activePanel: panel
        })
    }
    openView(view){
        this.setState({
            activeView: view
        })
    }
    toggleContext(){
        this.setState({ contextOpened: !this.state.contextOpened });
    }

    removeIcons(mapProps, map){
        map.setOptions({
            styles: mapStyle
        });
        $('.map').css('height', $(window).height() - $('.PanelHeader').height());
    }
    openCaptureModal(data){

    }
    onDragged(mapProps, map){
        this.setState({
            center: {lat: map.center.lat(), lng: map.center.lng()},
            zoom: map.zoom
        });
    }
    polyClick(area){
        this.openPanel('area');
        this.setState({activeArea: area});
    }
    clog(mapProps, map, e){
        console.log(`[${e.latLng.lat()}, ${e.latLng.lng()}],`);
    }

    render() {
        let user = this.state.users['U'+getParams('vk_user_id')];
        let curArea = this.state.activeArea;
        return (
            <Root activeView={this.state.activeView}>
                <View popout={this.state.popout} id="main" activePanel={this.state.activePanel}>
                    <Panel id="main">
                        <PanelHeader>
                            <PanelHeaderContent
                                aside={<Icon16Dropdown style={{ transform: `rotate(${this.state.contextOpened ? '180deg' : '0'})` }} />}
                                onClick={this.toggleContext} before={''} status={''}>
                                БИТВА ЗА ИНОРС
                            </PanelHeaderContent>
                        </PanelHeader>
                        <PanelHeaderContext opened={this.state.contextOpened} onClose={this.toggleContext}>
                            <UserHeaderContent areas={this.state.areas} polyClick={this.polyClick} user={user}/>
                        </PanelHeaderContext>
                        <MapMain
                            clog={this.clog}
                            areas={this.state.areas}
                            users={this.state.users}
                            center={this.state.center}
                            removeIcons={this.removeIcons}
                            google={this.props.google}
                            points={this.state.points}
                            markerClick={this.markerClick}
                            zoom={this.state.zoom}
                            polyClick={this.polyClick}
                            onDragged={this.onDragged}
                            polygons={polygons}
                        />
                        {this.state.snackbar}
                    </Panel>
                    <Panel id="area">
                        {this.state.areas.length ? (
                            <div>
                                <PanelHeader left={<PanelHeaderBack onClick={() => {
                                    this.openPanel('main')}}/>}>
                                    {this.state.areas[curArea].name}
                                </PanelHeader>
                                <AreaGroupMain users={this.state.users} areas={this.state.areas} curArea={curArea}/>
                                <AreaGroupPolitics areas={this.state.areas} curArea={curArea}/>
                                <AreaGroupActions openPanel={this.openPanel} areas={this.state.areas} curArea={curArea}/>
                                <AreaGroupHistory areas={this.state.areas} curArea={curArea}/>
                            </div>
                        ) : ''}
                    </Panel>
                    <Panel id="diversion">
                        <DiversionPanel areas={this.state.areas} curArea={curArea} user={user} users={this.state.users} openPanel={this.openPanel}/>
                    </Panel>
                    <Panel id="speech">
                        <SpeechPanel areas={this.state.areas} curArea={curArea} user={user} users={this.state.users} openPanel={this.openPanel}/>
                    </Panel>
                    <Panel id="laws">
                        <LawsPanel areas={this.state.areas} curArea={curArea} user={user} users={this.state.users} openPanel={this.openPanel}/>
                    </Panel>
                    <Panel id="trade">
                        <TradePanel areas={this.state.areas} curArea={curArea} user={user} users={this.state.users} openPanel={this.openPanel}/>
                    </Panel>
                    <Panel id="production">
                        <ProductionPanel areas={this.state.areas} curArea={curArea} user={user} users={this.state.users} openPanel={this.openPanel}/>
                    </Panel>
                    <Panel id="warehouse">
                        <WarehousePanel openPanel={this.openPanel} areas={this.state.areas} curArea={curArea}/>
                    </Panel>
                </View>
            </Root>
        );
    }
}
export default GoogleApiWrapper({
    apiKey: ("AIzaSyC2L8vPYtDn5Csq-7Gj9sZqSzvb3Bx1QfI")
})(App);
