import React from "react";
import {
    Button, Div,
    Group,
    Header, Input,
    PanelHeader,
    PanelHeaderBack, Progress, Select,
    SimpleCell,
    Slider
} from "@vkontakte/vkui";
import NotifSnack from "./NotifSnack";
import areasUpdate from "../functions/areasUpdate";
import check0100 from "../functions/check0100";
import Icon28MoonOutline from '@vkontakte/icons/dist/28/moon_outline';
import Icon28CheckCircleOutline from '@vkontakte/icons/dist/28/check_circle_outline';
import Icon32Spinner from '@vkontakte/icons/dist/32/spinner';
import Icon28CubeBoxOutline from '@vkontakte/icons/dist/28/cube_box_outline';
import economy from "../const/economy";
import getUnix from "../functions/getUnix";
import timeFormat from "../functions/timeFormat";

class ProductionPanel extends React.Component{
    diversionOffset = 60*30;
    constructor(props) {
        super(props);
        this.state = {
            snackbar: '',
            sliderVal: 1,
            selectVal: 0
        }
        this.pushProduction = this.pushProduction.bind(this);
        this.closeSnackbar = this.closeSnackbar.bind(this);
        this.openSnackbar = this.openSnackbar.bind(this);
        this.updateSelect = this.updateSelect.bind(this);
    }
    closeSnackbar(){
        this.setState({ snackbar: null })
    }
    openSnackbar(g,m){
        this.setState({
            snackbar: <NotifSnack closeSnackbar={this.closeSnackbar} good={g} message={m}/>
        });
    }
    diversion(e){
        let q = this.props.areas;
        let w = q[this.props.curArea];

        w.approval = check0100(w.approval);
        w.happiness = check0100(w.happiness);

        areasUpdate(q);
    }
    pushProduction(){
        const prodTime = [
            1,
            2,
            3,
            4,
            6,
            7,
            3,
            2,
            7,
            5
        ];
        let prodObj = {
            count: this.state.sliderVal,
            type: this.state.selectVal,
            ready: getUnix() + this.state.sliderVal*prodTime[this.state.selectVal]*10
        }

        let q = this.props.areas;
        let w = q[this.props.curArea];
        if(w.production.length >= 5){
            this.openSnackbar(false, 'Все ячейки заняты');
            return;
        }
        w.production.push(prodObj);
        q[this.props.curArea] = w;
        areasUpdate(q);
    }
    collectProd(i){
        let q = this.props.areas;
        let w = this.props.areas[this.props.curArea];
        if(w.warehouse[w.production[i].type] + w.production[i].count > 15000){
            this.openSnackbar(false, 'На складе нет места');
            return;
        }
        this.openSnackbar(true, 'Продукция доставлена на склад');
        q[this.props.curArea].warehouse[w.production[i].type] += w.production[i].count;
        q[this.props.curArea].production.splice(i,1);
        areasUpdate(q);
    }
    updateSelect(e){
        this.setState({
            selectVal: e.target.value
        });
    }
    render() {
        return (
            <div>
                <PanelHeader
                    left={<PanelHeaderBack onClick={() => {this.props.openPanel('area')}}/>}
                >
                    Производство
                </PanelHeader>
                <Group description="производите товары и продавайте их на торговой площадке">
                    <Div>
                        <Slider
                            step={1}
                            min={1}
                            max={100}
                            value={Number(this.state.sliderVal)}
                            onChange={v => {this.setState({sliderVal: v})}}
                            top="Кол-во товара"
                        />
                        <Input style={{margin: '15px 0'}} value={this.state.sliderVal} disabled/>
                        <Select value={this.state.selectVal} onChange={this.updateSelect} placeholder="Выберите продукцию">
                            {economy.map((v,i) => {
                                return (
                                    <option value={i}>{v}</option>
                                )
                            })}
                        </Select>
                        <Button onClick={this.pushProduction} style={{marginTop: 15}} size="xl">В очередь</Button>
                    </Div>
                </Group>
                <Group header={<Header mode="secondary">Очередь</Header>}>
                    {this.props.areas[this.props.curArea].production.map((v, i) => {
                        let before, description, click;
                        if(getUnix() - v.ready <= 0){
                            before = <Icon32Spinner width={28} height={28}/>;
                            let date = new Date(v.ready*1000);
                            let minutes = timeFormat(date.getMinutes());
                            let hours = timeFormat(date.getHours());
                            description = `будет готово в ${hours}:${minutes}`;
                            click = () => {return false;};
                        }
                        else{
                            before = <Icon28CheckCircleOutline/>;
                            description = 'нажмите, чтобы собрать';
                            click = () => {this.collectProd(i)}
                        }
                        return (
                            <SimpleCell onClick={click} key={v.ready+description} description={description} before={before}>
                                {`${economy[v.type]} (${v.count})`}
                            </SimpleCell>
                        )
                    })}
                    {/*<SimpleCell before={<Icon32Spinner width={28} height={28}/>} description="Будет готово в 42.12">*/}
                    {/*    Производится*/}
                    {/*</SimpleCell>*/}
                    {/*<SimpleCell before={<Icon28MoonOutline/>}>*/}
                    {/*    Пустая ячейка*/}
                    {/*</SimpleCell>*/}
                </Group>
                {/*<Group style={{display: 'none'}} header={<Header mode="secondary">Склад</Header>}>*/}
                {/*    <SimpleCell*/}
                {/*        before={<Icon28CubeBoxOutline/>}*/}
                {/*        description={<Progress value={24}/>}*/}
                {/*    >*/}
                {/*        Заполненность склада: 240/1000*/}
                {/*    </SimpleCell>*/}
                {/*    <Div>*/}
                {/*        <Button onClick={() => {this.props.openPanel('trade')}} size="xl">Отправить на торговую площадку</Button>*/}
                {/*    </Div>*/}
                {/*</Group>*/}
                {this.state.snackbar}
            </div>
        )
    }
}

export default ProductionPanel;