import {Group, Header, SimpleCell} from "@vkontakte/vkui";
import React from "react";
import getParams from "../../functions/getParams";

import Icon28SortHorizontalOutline from '@vkontakte/icons/dist/28/sort_horizontal_outline';
import Icon28DocumentOutline from '@vkontakte/icons/dist/28/document_outline';
import Icon28AdvertisingOutline from '@vkontakte/icons/dist/28/advertising_outline';
import Icon28BombOutline from '@vkontakte/icons/dist/28/bomb_outline';
import Icon28SettingsOutline from '@vkontakte/icons/dist/28/settings_outline';
import Icon28CubeBoxOutline from '@vkontakte/icons/dist/28/cube_box_outline';

const AreaGroupActions = (p) => {
    let activeArea = p.areas[p.curArea];
    if(activeArea.owner === 0){
        return '';
    }
    let content =
    <Group header={<Header mode="secondary">Взаимодействие</Header>}>
        <SimpleCell
            expandable={true}
            before={<Icon28SortHorizontalOutline/>}
            onClick={() => {p.openPanel('trade')}}
        >
            Торговля
        </SimpleCell>
        <SimpleCell
            expandable={true}
            before={<Icon28AdvertisingOutline/>}
        >
            Пропаганда
        </SimpleCell>
        <SimpleCell
            expandable={true}
            before={<Icon28BombOutline/>}
            onClick={() => {p.openPanel('diversion')}}
        >
            Диверсия
        </SimpleCell>
    </Group>;
    let contentOwner = (
        <Group header={<Header mode="secondary">Управление</Header>}>
            <SimpleCell
                expandable={true}
                before={<Icon28DocumentOutline/>}
                onClick={() => {p.openPanel('laws')}}
            >
                Законы
            </SimpleCell>
            <SimpleCell
                expandable={true}
                before={<Icon28SettingsOutline/>}
                onClick={() => {p.openPanel('production')}}
            >
                Производство
            </SimpleCell>
            <SimpleCell
                expandable={true}
                before={<Icon28CubeBoxOutline/>}
                onClick={() => {p.openPanel('warehouse')}}
            >
                Склад
            </SimpleCell>
            <SimpleCell
                expandable={true}
                before={<Icon28AdvertisingOutline/>}
                onClick={() => {p.openPanel('speech')}}
            >
                Выступить с речью
            </SimpleCell>
            <SimpleCell
                expandable={true}
                before={<Icon28SortHorizontalOutline/>}
                onClick={() => {p.openPanel('trade')}}
            >
                Торговля
            </SimpleCell>
        </Group>
    );
    if(activeArea.owner === parseInt(getParams('vk_user_id'))){
        return contentOwner;
    }
    return content;

}

export default AreaGroupActions;