import {Avatar, Snackbar} from "@vkontakte/vkui";
import React from "react";
import Icon16Done from '@vkontakte/icons/dist/16/done';
import Icon24Error from '@vkontakte/icons/dist/24/error';

const NotifSnack = (p) => {
    let blueBackground = {backgroundColor: 'var(--accent)'};
    let redBackground = {backgroundColor: 'var(--dynamic_red)'};
    return <Snackbar
        key={+new Date()}
        layout="vertical"
        onClose={() => p.closeSnackbar}
        before={<Avatar size={24} style={p.good ? blueBackground : redBackground}>
            {p.good ? <Icon16Done fill="#fff" width={14} height={14} />
                :<Icon24Error fill="#fff" width={14} height={14}/>
            }

        </Avatar>}
    >
        {p.message}
    </Snackbar>
}

export default NotifSnack;