import React from "react";
import economy from "../const/economy";
import {PanelHeader, PanelHeaderBack, Progress, SimpleCell} from "@vkontakte/vkui";
import Icon28CubeBoxOutline from '@vkontakte/icons/dist/28/cube_box_outline';

const WarehousePanel = (p) => {
    return (
        <div>
            <PanelHeader left={<PanelHeaderBack onClick={() => {p.openPanel('area')}}/>}>
                Склад
            </PanelHeader>
            {economy.map((v,i) => {
                return (
                <SimpleCell key={v} before={<Icon28CubeBoxOutline/>} description={`${p.areas[p.curArea].warehouse[i]}/15000`}>
                    {v}
                </SimpleCell>
                )
                })
            }
        </div>
    )
}

export default WarehousePanel;