import React from "react";
import {Group, PanelHeader, PanelHeaderBack, SimpleCell} from "@vkontakte/vkui";
import NotifSnack from "./NotifSnack";
import Icon24Flash from '@vkontakte/icons/dist/24/flash';
import Icon24Error from '@vkontakte/icons/dist/24/error';
import fire from "../functions/firebase";
import getParams from "../functions/getParams";
import getRand from "../functions/getRand";
import usersUpdate from "../functions/usersUpdate";
import areasUpdate from "../functions/areasUpdate";
import eventToPush from "../functions/eventToPush";
import check0100 from "../functions/check0100";
import getUnix from "../functions/getUnix";
import timeFormat from "../functions/timeFormat";

class SpeechPanel extends React.Component{
    diversionOffset = 60*30;
    constructor(props) {
        super(props);
        this.state = {
            snackbar: ''
        }
        this.diversion = this.diversion.bind(this);
        this.closeSnackbar = this.closeSnackbar.bind(this);
        this.openSnackbar = this.openSnackbar.bind(this);
    }
    closeSnackbar(){
        this.setState({ snackbar: null })
    }
    openSnackbar(g,m){
        this.setState({
            snackbar: <NotifSnack closeSnackbar={this.closeSnackbar} good={g} message={m}/>
        });
    }
    diversion(e){
        let timeStamp = getUnix();
        let lastDiversion = this.props.user.lastSpeech;
        if(timeStamp - lastDiversion < this.diversionOffset){
            this.openSnackbar(false, 'Речь можно произносить раз в 30 минут');
            return 0;
        }
        let q = this.props.areas;
        let w = q[this.props.curArea];
        // eslint-disable-next-line default-case
        switch (e) {
            case 0:
                if(getRand(0,4) > 0){
                    let s = getRand(1,5);
                    w.approval += s;
                    w.events.push(eventToPush('speech', 0, 'approval', s));
                    this.openSnackbar(true, `Уровень поддержки повышен на ${s}`);
                }
                else{
                    this.openSnackbar(false, 'Речь никого не впечатлила');
                }
                break;
        }
        w.approval = check0100(w.approval);
        w.happiness = check0100(w.happiness);
        q[this.props.curArea] = w;

        let z = this.props.users;
        let x = this.props.user;

        x.lastSpeech = getUnix();
        z['U'+getParams('vk_user_id')] = x;

        areasUpdate(q);
        usersUpdate(z);
    }
    render() {
        let timeStamp = getUnix();
        let lastDiversion = this.props.user.lastSpeech;
        let date = new Date(lastDiversion*1000+this.diversionOffset*1000);

        let minutes = timeFormat(date.getMinutes());
        let hours = timeFormat(date.getHours());


        let divSpeech = (this.props.areas[this.props.curArea].events.length !== 0);
        if(divSpeech){
            divSpeech = this.props.areas[this.props.curArea].events[this.props.areas[this.props.curArea].events.length - 1].type === 'diversion';
        }
        else{
            divSpeech = false;
        }

        return (
            <div>
                <PanelHeader
                    left={<PanelHeaderBack onClick={() => {this.props.openPanel('area')}}/>}
                >
                    Выступить с речью
                </PanelHeader>
                <Group description="произнесите воодушевляющую речь, которая может поднять настроение и укрепить ваши позиции, а может и наоборот. в общем, следите за базаром">
                    <SimpleCell multiline={true} style={timeStamp - lastDiversion > this.diversionOffset ? {display: 'none'} : {}} before={<Icon24Error width={28} height={28}/>}>
                        {`Произнести следующую речь можно в ${hours}:${minutes}`}
                    </SimpleCell>
                    <SimpleCell
                        onClick={() => {this.diversion(0)}}
                        before={<Icon24Flash width={28} height={28}/>}
                        expandable={true}
                        style={!divSpeech ? {display: 'none'} : {}}
                    >Свалить все на диверсии</SimpleCell>
                </Group>
                {this.state.snackbar}
            </div>
        )
    }
}

export default SpeechPanel;