import {Map, Marker, Polygon} from "google-maps-react";
import warehouse from "../img/wh.png";
import pivo from "../img/pv.png";
import React from "react";
import UMPO from '../img/UMPO.png';
import {ScreenSpinner} from "@vkontakte/vkui";

const MapMain = (p) => {
    if(!(p.areas.length && p.users['U1'])){
        return <ScreenSpinner/>;
    }
    let polygons = p.polygons;

    let polyToDraw = [];
    for (let i = 0; i < polygons.length; i++){
        polyToDraw[i] = [];
        for (let k = 0; k < polygons[i].length; k++){
            polyToDraw[i][k] = {lat: 0, lng: 0};
            polyToDraw[i][k].lat = polygons[i][k][0];
            polyToDraw[i][k].lng = polygons[i][k][1];
        }
    }
    return (
        <Map
            onReady={(mapProps, map) => p.removeIcons(mapProps, map)}
            google={p.google}
            zoom={p.zoom}
            className="map"
            streetViewControl={false}
            mapTypeControl={false}
            scaleControl={false}
            fullscreenControl={false}
            zoomControl={false}
            onBoundsChanged={p.onDragged}
            initialCenter={p.center}
            onClick={p.clog}
        >
            {polyToDraw.map((v, i) => {
                return (
                    <Polygon
                        onClick={() => {p.polyClick(i)}}
                        paths={v}
                        key={p.areas[i].owner+"poly"+v[0].lat+' '+v[1].lng}
                        strokeColor="#000000"
                        strokeOpacity={0.8}
                        strokeWeight={0.5}
                        fillColor={p.areas[i].owner === 0 ? '#000000' : p.users['U'+p.areas[i].owner].color}
                        fillOpacity={0.35} />
                );
            })}
            {polyToDraw.map(value => value.map(
                x => (
                    <Marker
                        num={1488}
                        title="Склад певка"
                        name="Склад певка"
                        background=""
                        description=""
                        taken="0"
                        onClick={p.clog}
                        position={x}
                    />
                )
            ))}

            {/*<Marker*/}
            {/*    num={1488}*/}
            {/*    title="Склад певка"*/}
            {/*    name="Склад певка"*/}
            {/*    background=""*/}
            {/*    description=""*/}
            {/*    taken="0"*/}
            {/*    onClick={p.markerClick}*/}
            {/*    position={*/}
            {/*        {lat: 54.777251,*/}
            {/*            lng: 56.127483}*/}
            {/*    }*/}
            {/*    icon={{*/}
            {/*        url: warehouse,*/}
            {/*        // eslint-disable-next-line no-undef*/}
            {/*        anchor: new google.maps.Point(32,32),*/}
            {/*        // eslint-disable-next-line no-undef*/}
            {/*        scaledSize: new google.maps.Size(32,32)*/}
            {/*    }}*/}
            {/*/>*/}
            {/*<Marker*/}
            {/*    num={1337}*/}
            {/*    title="РАБота"*/}
            {/*    name="УМПО"*/}
            {/*    background=""*/}
            {/*    description=""*/}
            {/*    taken="0"*/}
            {/*    onClick={p.markerClick}*/}
            {/*    position={*/}
            {/*        {lat: 54.788578,*/}
            {/*            lng: 56.131215}*/}
            {/*    }*/}
            {/*    icon={{*/}
            {/*        url: UMPO,*/}
            {/*        // eslint-disable-next-line no-undef*/}
            {/*        anchor: new google.maps.Point(64,32),*/}
            {/*        // eslint-disable-next-line no-undef*/}
            {/*        scaledSize: new google.maps.Size(64,32)*/}
            {/*    }}*/}
            {/*/>*/}
            {/*{p.points.map(value =>*/}
            {/*    <Marker*/}
            {/*        key={value.name+value.lat}*/}
            {/*        num={value.id}*/}
            {/*        id={value.id}*/}
            {/*        title={value.name}*/}
            {/*        name={value.name}*/}
            {/*        background={value.background}*/}
            {/*        description={value.description}*/}
            {/*        taken={value.taken}*/}
            {/*        onClick={p.markerClick}*/}
            {/*        position={*/}
            {/*            {lat: value.lat,*/}
            {/*                lng: value.lon}*/}
            {/*        }*/}
            {/*        icon={{*/}
            {/*            url: pivo*/}
            {/*        }}*/}
            {/*    />*/}
            {/*)}*/}
        </Map>
    )
}

export default MapMain;