import getParams from "./getParams";
import getUnix from "./getUnix";

const eventToPush = (t,n,et,ev) => {
    return {
        type: t,
        num: n,
        date: getUnix(),
        effect: {
            type: et,
            value: ev
        },
        actor: getParams('vk_user_id')
    }
}

export default eventToPush;