import {Group, Header, Placeholder, SimpleCell} from "@vkontakte/vkui";
import React from "react";

import Icon24Error from '@vkontakte/icons/dist/24/error'
import Icon56InfoOutline from '@vkontakte/icons/dist/56/info_outline';
import Icon28AdvertisingOutline from '@vkontakte/icons/dist/28/advertising_outline';

import timeFormat from "../../functions/timeFormat";

const AreaGroupHistory = (p) => {
    let activeArea = p.areas[p.curArea];
    if(activeArea.owner === 0){
        return '';
    }
    const eventTypes = {
        diversion: {
            name: 'Диверсия',
            icon: <Icon24Error/>,
            types: ['перерезать кабели']
        },
        speech: {
            name: 'Речь',
            icon: <Icon28AdvertisingOutline width={24} height={24}/>,
            types: ['свалить вину на диверсии']
        }
    }
    const months = ['янв', 'фев', 'мар', 'апр', 'мая', 'июн', 'июл', 'авг', 'сен', 'окт', 'ноя', 'дек'];
    return (
        <Group header={<Header mode="secondary">События</Header>}>
            {activeArea.events.length ?
                activeArea.events.reverse().map(
                    v => {
                        let date = new Date(v.date*1000);
                        let minutes = timeFormat(date.getMinutes());
                        let hours = timeFormat(date.getHours());

                        let time = `${date.getDate()} ${months[date.getMonth()]} ${hours}:${minutes}`

                        return (
                            <SimpleCell
                                before={eventTypes[v.type].icon}
                                description={time}>
                                {`${eventTypes[v.type].name}: ${eventTypes[v.type].types[v.num]}`}
                            </SimpleCell>
                        )
                    }
                ) : <Placeholder
                    icon={<Icon56InfoOutline/>}
                    header="Событий пока нет"
                >
                    Как только на этой территории что-то произойдет,<br/>
                    оно отразится здесь
                </Placeholder>

            }
        </Group>)
}

export default AreaGroupHistory;