import React from "react";
import {Group, Header, PanelHeader, PanelHeaderBack, Placeholder, SimpleCell} from "@vkontakte/vkui";
import NotifSnack from "./NotifSnack";
import Icon24Flash from '@vkontakte/icons/dist/24/flash';
import Icon24Error from '@vkontakte/icons/dist/24/error';
import getParams from "../functions/getParams";
import getRand from "../functions/getRand";
import usersUpdate from "../functions/usersUpdate";
import areasUpdate from "../functions/areasUpdate";
import eventToPush from "../functions/eventToPush";
import check0100 from "../functions/check0100";
import getUnix from "../functions/getUnix";
import timeFormat from "../functions/timeFormat";
import Icon56RecentOutline from '@vkontakte/icons/dist/56/recent_outline';
import economy from "../const/economy";

class TradePanel extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            snackbar: ''
        }
        this.diversion = this.diversion.bind(this);
        this.closeSnackbar = this.closeSnackbar.bind(this);
        this.openSnackbar = this.openSnackbar.bind(this);
    }
    closeSnackbar(){
        this.setState({ snackbar: null })
    }
    openSnackbar(g,m){
        this.setState({
            snackbar: <NotifSnack closeSnackbar={this.closeSnackbar} good={g} message={m}/>
        });
    }
    diversion(e){

    }
    render() {
        let h = (new Date()).getHours();
        if(h >= 8 && h < 22){
            return (
                <div>
                    <PanelHeader
                        left={<PanelHeaderBack onClick={() => {this.props.openPanel('area')}}/>}
                    >
                        Торговая площадка
                    </PanelHeader>
                    <Group header={<Header mode="secondary">Купить</Header>}>
                        {economy.map((v,i) => {
                            return <SimpleCell
                                key={`buy${i}`}
                                onClick={() => {this.diversion(i)}}
                                before={<Icon24Flash width={28} height={28}/>}
                                expandable={true}
                                description={`мин. цена 42`}
                            >{v}</SimpleCell>
                        })}
                    </Group>
                    {this.state.snackbar}
                </div>
            )
        }
        else{
            return <div>
                <PanelHeader
                    left={<PanelHeaderBack onClick={() => {this.props.openPanel('area')}}/>}
                >
                    Закрыто
                </PanelHeader>
                <Placeholder
                    icon={<Icon56RecentOutline/>}
                    header="Закрыто"
                >
                    Торговая площадка работает с 8 по 22
                </Placeholder>
            </div>
        }
    }
}

export default TradePanel;